.footer {
    background-color: #eaeaea;
    height: 60px;
    padding: 0 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer-txt {
    font-size: 12px;
    color: #999;
}

ul {
    display: flex;
}

ul li {
    list-style: none;
}

ul li a {
    color: #999;
    margin: 0 5px;
    transition: color .5s linear;
}

ul li a:hover {
    color: #4b4b4b;
}